import { Envelope, Lock } from "phosphor-react";
import { FormEvent, useState } from "react";
import axios from 'axios';
import { Button } from "../components/Button";
import { CheckBox } from "../components/Checkbox";
import { Heading } from "../components/Heading";
import { TextInput } from "../components/Input";
import { Text } from "../components/Text";
import { Logo } from "../Logo";

export function SignIn() {
  const [isUserSignedIn, setIsUserSignedIn] = useState(false);

  function handleSubmit(event: FormEvent) {
    event.preventDefault();
    axios.post('/sessions', {
      email: 'email@gmail.com',
      password: '12345689'
    })
    setIsUserSignedIn(true);
  }
    return (
        <div className='w-screen h-screen bg-gray-900 flex flex-col items-center justify-center text-gray-100'>
      <header className='flex flex-col items-center'>
        <Logo />
        <Heading size='lg' className='mt-4'>
          Ignite Lab
        </Heading>
        <Text size='lg' className='text-gray-400 mt-1'>
          Faça Login e começe a usar
        </Text>
      </header>

      <form onSubmit={handleSubmit} className='flex flex-col gap-4 items-stretch w-full max-w-sm mt-4'>
        { isUserSignedIn && <Text>Login realizado</Text> }
        <label htmlFor='email' className='flex flex-col gap-3'>
          <Text className='font-semibold'>Endereço de e-mail</Text>
          <TextInput.Root>
            <TextInput.Icon>
              <Envelope />
            </TextInput.Icon>
            <TextInput.Input type="email" id="email" placeholder='Digite seu e-mail'/>
          </TextInput.Root>
        </label>

        <label htmlFor='password' className='flex flex-col gap-3'>
          <Text className='font-semibold'>Sua senha</Text>
          <TextInput.Root>
            <TextInput.Icon>
              <Lock />
            </TextInput.Icon>
            <TextInput.Input type="password" id="password" placeholder='****************'/>
          </TextInput.Root>
        </label>
        <label htmlFor='remenber' className='flex items-center gap-2'>
          <CheckBox id='remenber' />
          <Text size='sm' className='text-gray-200'>Lembrar de mim por 30 dias</Text>
        </label>
        <Button type='submit' className='mt-2'>Entrar na plataforma</Button>
      </form>

      <footer className='flex flex-col items-center gap-4 mt-8'>
        <Text asChild size='sm'>
          <a href='' className='text-gray-400 underline'>Esqueceu sua senha?</a>
        </Text>
        <Text asChild size='sm'>
          <a href='' className='text-gray-400 underline'>Não possui conta? Crie uma agora!</a>
        </Text>
      </footer>

    </div>
    )
}